@media only screen and (min-width : 1140px){
  .m-position {
    .p-header {
      .p-col {
        font-size: 14px;
      }
    }
    .p-body {
      .p-row {
        font-size: 14px;
      }
    }
  }
}
.m-position {
  margin-top : 16px;
  font-size : 14px;
  overflow: auto;
  white-space: nowrap;
  -ms-overflow-style: none;  /* Internet Explorer 10+ */
  scrollbar-width: none;  /* Firefox */
  &::-webkit-scrollbar { 
    display: none;  /* Safari and Chrome */
  }
  .p-header {
    display : flex;
    justify-content: space-between;
    height: 40px;
    align-items : center;
  }
  .p-body {
    font-size : 12px;
    height : 184px;
    overflow : auto;
  }
  .p-row {
    display: flex;
    justify-content: space-between;
    color : #E0ECFF;
    align-items: center;
    height: 32px;
  }
  .p-col.red {
    color : #CE5E5E;
  }
  .p-col.green {
    color : #59AE99;
  }
  .p-col {
  .red {
    color : #CE5E5E;
  }
  }
  .p-col {
  .green {
    color : #59AE99;
  }
  }
  .p-col {
    .gray{
      color : #93A1B8;
    }
  }
  .p-col {
    width : 95px;
    margin-right : 16px;
  }
  .p-col.close-position{
    cursor: pointer;
  }
}